<template>
  <div class="orderinfo-box">
    <common-header :tittle="tittle" :showleft="true" :showright="true" :routerLeft="routerLeft"></common-header>
    <div class="orderinfo-content">
      <img :src="imgFileUrl+data.carProductPic" alt="">
      <li>
        <p><span>{{data.productNm}}<i>{{data.carProductVer}}</i></span>
          <span @click="parameter(data)">参数配置</span>
        </p>
        <p class="dis">{{data.carProductStyle}}</p>
        <div class="dis"><div class="left">容量：<span >{{data.carPowerBatkwh}}</span></div><div class="right">续航里程：<span>{{data.carPowerMaxKm}}</span></div></div>
        <div  class="dis"><div class="left">保修政策：<span></span></div><div class="right">成交价格：<span>{{data.carProductPrice}}万</span></div></div>
        <div  class="dis"><div class="left">指导价格：<span>{{data.guidingPrice}}</span></div><div class="right">金融服务：<span>{{data.orderPayStr}}</span></div></div>
        <div  class="dis"><span>销售商<span>{{data.partnerNm}}</span></span><span>销售商名称</span></div>
      </li>
      <div class="line-f">&nbsp;</div>
      <div class="step">
        <h5>订单状态</h5>
        <div class="step-list">
          <van-steps direction="vertical" :active="active" >
            <van-step v-for="item in data.nodeList">
              <h3>{{item.codedesc}}</h3>
              <p>{{item.lastUpdTime}}</p>
            </van-step>
          </van-steps>

        </div>
      </div>
      <div class="line-f">&nbsp;</div>
      <div class="submitBtn">
        <button @click="phone">联系电话</button>
      </div>
    </div>
     <van-dialog  v-model="showFriend"  show-cancel-button :before-close="beforeClose" confirm-button-text="确定" >
        <van-field  label="手机号" v-model="bindphone"  class="dialog-inp"/>
    </van-dialog>
  </div>
</template>

<script>
import { Step, Steps } from 'vant'
import commonHeader from 'common/common-header'
import { Dialog, Toast } from 'vant'
import * as homeApi from '../../api/home-api'
import { imgFileUrl } from '../../apiconfig/index'
export default {
  name: 'drawbackinfo',
  components: { Step, Steps, commonHeader },
  data() {
    return {
      tittle: '退款明细',
      routerLeft: '/orderlist',
      showFriend:false,
      bindphone:localStorage.getItem('phone'),
      imgFileUrl:imgFileUrl,
      data:'',
      active:'0'
    }
  },
  methods: {
    active(){
      if(this.active==0){
        this.active=0
      }else{
        this.active=this.active-1
      }
    },
    parameter(data) {
      localStorage.setItem('carProductKind', data.carProductKind)
      localStorage.setItem('carProductCd', data.carProductCd)
      this.$router.push({path: '/parameter'})
    },
    phone(){
          this.showFriend=true
    },
     beforeClose(action, done){
       if (action === 'confirm') {
         window.location.href = 'tel://'
         done();
       } else {
         done();
       }
     },
    getorderinfo(){
      let params={
        id:this.$route.params.orderid
      }
      homeApi.orderinfo(params).then(res=>{
        this.data=res.data.data
        let nodelist=res.data.data.nodeList
        this.active= nodelist.findIndex(fruit => fruit.used === false);
        this.active()
      }).catch(err=>{
        console.log(err)
      })
    }
  },
  mounted() {
    console.log(this.$route.params.info)
    this.getorderinfo()
  }

}
</script>
<style scoped lang="less">
  @import "~styles/index.less";
  @import "~styles/variable.less";
  .orderinfo-box{
    width: 100%;
    height: auto;
      .van-dialog{
        padding-top:0.4rem;
        .dialog-inp{
          height: 1rem!important;
          line-height: 1rem;
          text-align: left;
          padding-left: 0.3rem;
        }
      }
    .orderinfo-header{
      width: 100%;
      height: 1.35rem;
      border-bottom: 1px solid #F3F3F3;
      display: flex;
      justify-content: space-between;
      align-items: center;
      background: #fff;
      .fs(35);
      li{
        padding-left: 0.2rem;
        padding-right: 0.2rem;
        img:nth-child(1){
          width: 0.3rem;
          height: 0.5rem;
        }
        img:nth-child(2){
          width: 0.6rem;
          height: 0.5rem;
        }
      }

    }
    .orderinfo-content{
      background-color: #ffffff;
      width: 100%;
      height: auto;
      .pl(10);
      .pr(10);
      >li{
        background: #fff;
        text-align: left;
        padding:0.4rem;
        line-height: 0.8rem;
        .fs(30);
        p:nth-child(2){
        }
        p:nth-child(1){
          .fs(35);
          display: flex;
          justify-content: space-between;
          span:nth-child(1){
            font-weight:bold;
            letter-spacing: 0.03rem;
            i{
              .ml(15);
              font-style:normal;
              font-weight:bold;
            }
          }
          span:nth-child(2){
            .fs(30);
            color: #FF4014;
          }

        }
        .dis{
          width: 100%;
          display: flex;
          justify-content: space-between;
          .left{
            span{
              color: #666666;
            }
          }
          .right{
            text-align: left;
            width: 50%;
            span{
              color: #666666;
            }
          }
        }
        .dis:last-child{
          width: 80%;
        }
      }
      .btn{
        width: 100%;
        padding:0 0.4rem;
        background: #fff;
        button{
          width: 100%;
          background: #C6DAF6;
          color: #000;
          .fs(33);
        }

      }
      .text{
        width: 100%;
        background: #fff;
        p{
          width: 50%;
          height: 1.3rem;
          margin: 0 auto;
          display: flex;
          .fs(30);
          justify-content: space-between;
          align-items: center;
          span:nth-child(1){
            color: #666666;
          }
          span:nth-child(2){
            color: #FF4014;
          }
        }
      }
      .line-f{
        .h(20);
        background-color:#f5f5f5;
      }
      .step{
        .padding(20,30);
        h5{
          .fs(35);
          color:#ff7414;
          text-align: left;
        }
        .step-list{
          .margin(0,60);
          .fs(30);
          text-align: left;
          h3{
            .pt(20);
            .pr(20);
            .pb(8);
            .pl(20);
          }
          p{
            .pr(20);
            .pb(20);
            .pt(8);
            .fs(28);
            .pl(20);
            color:#999999;
          }
          .van-steps--vertical{
            .padding(40,50);
          }
          .van-step__title{
            .padding(40,40);
          }
          .van-step__circle-container {
            position: absolute;
            top: 0px;
            left: 0px;
            padding: 0 0px;
            background-color: #fff;
            z-index: 999999;
          }
          .van-step__circle-container{
            position: absolute;
            top: 0px;
            left: 0px;
          }
          [class*=van-hairline]::after{
            border: none;
          }
        }
      }
      .business{
        .padding(20,30);
        text-align: left;
        h5{
          .fs(35);
          color:#ff7414;
          .pb(20);
        }
        p{
          .fs(28);
          .padding(6,20);
        }
      }
      .submitBtn{
        .pt(30);
        .pb(10);
        button{
          .fs(33);
          width:100%;
          border:none;
          color: #ffffff;
          background-color: #ff7414;
          .padding(20,0);
        }
      }
    }
    img{
      width: 100%;
    }
  }
</style>
<style>
  .van-step__circle {
    display: block;
    width: 10px!important;
    height: 10px!important;
    border-radius: 50%;
    background-color: #969799;
    top: 16px;
    left: -19px!important;
  }
  .van-icon-checked:before{
    font-size:16px;
  }
  .van-step--vertical.van-step--process .van-icon {
    top: 12px;
    left: -22px;
    line-height: 1;
    font-size: 12px;
  }
  /*.van-step--finish .van-step__circle, .van-step--finish .van-step__line{
    content: "\F02F";
    font-size:16px;
  }
  .van-step--finish .van-step__circle:before{
    content: "\F02F";
    font-size:16px;
  }*/
</style>



